.p{
    margin: 8vh auto 2vh;
    font-size: 2.2em;
    font-weight: 700;
	color: #2f2f28;
}
.modalContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20vh;
    text-align: center;
}
.openModalBtn{
	z-index: 1;
	background-color: #af1c1c;
	position: relative;
	display: inline-block;
	font-size: 18px;
	padding: 10px 25px;
	color: #f0effe;
	text-decoration: none;
	border: solid 5px #f0effe;
	/*box-shadow: 15px 15px 0  -5px #31a2ac;*/
	overflow: hidden;
	text-transform: uppercase;
	letter-spacing: 2px;
    outline: 5px solid #2f2f28;
	cursor: pointer;
}
.openModalBtn:before {
	display: inline-block;
	position: absolute;
	transform: skew(-50deg);
	top: 0px;
	left: 260px;
	background-color: #2bb673;
	content: "　";
	width: 350px;
	height: 120px;
	z-index: -1;
}  
.openModalBtn:hover:before {
	top: -40px;
	left: -40px;
}
.overlay{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    transition: 0.5s ease-in-out;
}
.modal{
    background-color: #f0effe;
    width: 70%;
    max-width: 500px;
    max-height: 300px;
    padding: 40px 30px;
    border-radius: 30px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
    transition: 0.5s ease-in-out;
}
.modal:before, .modal:after {
    content: '';
    display: block;
    height: 50px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}
.modal:before {
    width: 95%;
    bottom: -10px;
    background-color: #f0effeb0; 
}
.modal:after{
    width: 90%;
    bottom: -20px;
    background-color: #f0effe52;
}
.modal img{
    border-radius: 5px;
    width: 100%;
}
.modal svg{
    position: absolute;
    right: 10px;
    top: 10px;
    height: 24px;
    width: 24px;
    opacity: 0.3;
    cursor: pointer;
}
.modal svg:hover {
    opacity: 1;
}
.overlay.animated {
    opacity: 0;
    visibility: hidden;
}
.overlay.animated .modal{
    opacity: 0;
    visibility: hidden;
    transform: translate(-50%, -30%);
    transition-delay: 0.2s;

}
.overlay.animated.show{
    opacity: 1;
    visibility: visible;
    position: fixed;
    z-index: 100;
}
.overlay.animated.show .modal {
    opacity: 1;
    visibility: visible;
    transform: translate(-50%, -50%);
}
.text{
    font-size: 1.2em;
    font-weight: 700;
	color: #2f2f28;
}
.success{
    width: 120px;
    height: 120px;
    cursor: pointer;
}
