header{
    position: relative;
    width: 100%;
}
.headerBg{
    background-color: #2f2f28;
}
.content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 5vh;
    max-width: 1200px;
    margin: 0 auto;
}
header p{
    
    color: #f0effe;
}
header a{
    color: #31a2ac;
}
@media (max-width: 1000px) {
    .content{
        height: auto;
    }
}