.todoContainer{
    width: 30vw;
    background-color: #2f2f28;
    margin: 7vh auto 0;
    padding: 20px;
    border-radius: 30px;
    position: relative;
}
.todoContainer:before, .todoContainer:after {
    content: '';
    display: block;
    height: 50px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}
.todoContainer:before {
    width: 95%;
    bottom: -10px;
    background-color: #2f2f28a8;
    
}
.todoContainer:after {
    width: 90%;
    bottom: -20px;
    background-color: #2f2f2850;
    
}
.inputContainer input{
    height: 4vh;
    border: solid 5px #2f2f28;
    outline: 5px solid #f0effe;
    border-radius: none;
    background-color: #31a2ac;
    color: #f0effe;
}

.empty p{
    font-size: 1.4vw;
	font-weight: 700;
	color: #f0effe;
}
h2{
    font-size: 2.0vw;
	font-weight: 700;
    color: #f0effe;
}
.todo{
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.todo li{
    font-size: 1.4vw;
	font-weight: 700;
    color: #f0effe;
}
.addButton{
    height: 6vh;
    width: 8vw;
    z-index: 1;
    background-color: #31a2ac;
    position: relative;
    display: inline-block;
    font-size: 1.4vw;
    padding: 5px 15px;
    color: #f0effe;
    text-decoration: none;
    border: solid 5px #f0effe;
    box-shadow: 10px 10px 0  -5px #af1c1c;
    transition: .3s;
    overflow: hidden;
    text-transform: uppercase;
    letter-spacing: 2px;
    cursor: pointer;
    margin: 0 20px;
}
.addButton:before {
    display: inline-block;
    transition: bottom, 0.35s;
    position: absolute;
    transform: skew(-50deg);
    top: 0px;
    left: 260px;
    background-color: #af1c1c;
    content: "　";
    width: 300px;
    height: 120px;
    z-index: -1;
}  
.addButton:hover {
    box-shadow: 0px 0px 0  0px #31a2ac;
}
.addButton:hover:before {
    top: -40px;
    left: -40px;
}
.deleteButton{
	z-index: 1;
	background-color: #af1c1c;
	position: relative;
	display: inline-block;
	font-size: 0.6vw;
	padding: 5px 15px;
	color: #f0effe;
	text-decoration: none;
	border: solid 3px #2f2f28;
	box-shadow: 10px 10px 0  -5px #31a2ac;
	transition: .3s;
	overflow: hidden;
	text-transform: uppercase;
	letter-spacing: 2px;
    outline: 2px solid #f0effe;
	cursor: pointer;
    margin: 5px 0px 20px 20px;
    font-weight: 700;
}
.deleteButton:before {
	display: inline-block;
	transition: bottom, 0.35s;
	position: absolute;
	transform: skew(-50deg);
	top: 0px;
	left: 260px;
	background-color: #31a2ac;
	content: "　";
	width: 300px;
	height: 120px;
	z-index: -1;
}  
.deleteButton:hover {
	box-shadow: 0px 0px 0  0px #31a2ac;
}
.deleteButton:hover:before {
	top: -40px;
	left: -40px;
}