.p{
    font-size: 2.2em;
    font-weight: 700;
	color: #2f2f28;
}
.quiz {
    width: 500px;
    border-radius: 30px;
    padding: 40px;
    background-color: #2f2f28;
    position: relative;
    margin: 0 auto;
    color: #f0effe;
}
.result {
    text-align: center;
}
.result button {
    font-weight: bold;
    border-radius: 30px;
    border: 3px solid #31a2ac;
    padding: 15px 30px;
    font-size: 20px;
    background-color: #f0effe;
    color: #2f2f28;
    margin-top: 20px;
    cursor: pointer;
}
.result button:hover {
    border: 3px solid #af1c1c;
    color: #af1c1c;
}
.result h2 {
    margin-bottom: 0;
    color: #f0effe;
}
.result img {
    width: 150px;
}
.progress {
    height: 10px;
    border-radius: 30px;
    background-color: #f0effe;
    margin-bottom: 25px;
    border: 3px solid #31a2ac;
}
.progress__inner {
    height: 100%;
    border-radius: 30px;
    width: 80%;
    transition: all 0.3s ease-in-out;
    background: #af1c1c;
}
.quiz:before, .quiz:after {
    content: '';
    display: block;
    height: 50px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}
.quiz:before {
    width: 95%;
    bottom: -10px;
    background-color: #2f2f28a8;
    z-index: 0;
}
.quiz:after {
    width: 90%;
    bottom: -20px;
    background-color: #2f2f2850;
    z-index: 1;
}
.quiz h1 {
    margin: 0;
}
.quiz ul {
    list-style: none;
    padding: 0;
}
.quiz ul li {
    padding: 15px;
    border: 3px solid #31a2ac;
    border-radius: 15px;
    margin-bottom: 10px;
    cursor: pointer;
    transition: all 0.1s ease-in-out;
    color: #2f2f28;
    background-color: #f0effe;
}
.quiz ul li:hover {
    border: 3px solid #af1c1c;
    color: #af1c1c;
}