.calcContainer{
	width: 30vw;
    background-color: #2f2f28;
    margin: 7vh auto 0;
    padding: 20px;
	padding-bottom: 2.4em;
    border-radius: 30px;
    position: relative;
}
.calcContainer:before, .calcContainer:after {
    content: '';
    display: block;
    height: 50px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}
.calcContainer:before {
    width: 95%;
    bottom: -10px;
    background-color: #2f2f28a8;
    
}
.calcContainer:after {
    width: 90%;
    bottom: -20px;
    background-color: #2f2f2850;
}
.p{
	font-size: 2.8vw;
	font-weight: 700;
	color: #f0effe;
}
.wrapper{
	max-width: 25vw;
	min-height: 25vh;
	margin: 0 auto;
}
  
button{
	font-size: 2.0em;
	border: 2px solid #2f2f28;
	cursor: pointer;
}
.modifiers button{
	background-color: #af1c1c;
}
.operations button{
	background-color: #31a2ac;
	width: 100px;
}
.digits button{
	background-color: #f0effe;
}
.wrapper, .subgrid{
	display: grid;
}
.wrapper{
	grid-template-areas:
		"input input input input"
		"total total total total"
		"modif modif modif oper"
		"digit digit digit oper"
		"digit digit digit oper"
		"digit digit digit oper"
		"digit digit digit oper";
	grid-auto-columns: 1fr;
}
.containerInput{
	background-color: #31a2ac;
	height: 7vh;
	width: 25vw;
	margin: 0 auto;
	margin-bottom: 2vh;
	font-size: 2.2em;
	border: 5px solid #f0effe;
	grid-area: input;
}
.showInput{
	grid-area: total;
}
.modifiers{
	grid-area: modif;
}
.operations{
	grid-area: oper;
}
.digits{
	grid-area: digit;
}
.show-input{
	display: flex;
	justify-content: flex-start;
	align-items: center;
	font-size: 2.0em;
}
.modifiers{
	grid-auto-flow: column;
	grid-auto-columns: 1fr;
}
  .digits{
	display: flex;
	flex-wrap: wrap;
}
.digits button{
	flex: 1 0 26%;
}
  
  