.portfolio{
    background-color: #f0effe;
    border-top: 3px solid #2f2f28;
    padding: 0 0 5vh;
	animation-duration: 2s;
	animation-name: opacity;
	border-bottom: 5px solid #31a2ac;
}
.text{
    font-size: 1.4vw;
	font-weight: 700;
	color: #2f2f28;
}
.display{
	background-color: #f0effe;
	border-top: 3px solid #2f2f28;
	min-height: 75vh;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 30px;
	font-weight: 700;
	padding: 20px;
	color: #2f2f28;
	display: none;
}
@keyframes opacity {
	from {
	  opacity: 0;
	}
  
	to {
	  opacity: 1;
}
}
@media ( max-width: 1280px ) {
    .display {
		display: flex;
    }
	.portfolio {
		display: none;
    }
}