.p{
    margin: 8vh auto 2vh;
    font-size: 2.2em;
    font-weight: 700;
	color: #2f2f28;
}
.inviteList{
    width: 25vw;
    height: 75vh;
    margin: 2vh auto;
    background-color: #f0effe;
    border-radius: 30px;
    box-shadow: 5px 5px 5px #2f2f2877;
    padding: 40px;
    border: 3px solid #31a2ac;
}
.search{
    position: relative;
}
.search input {
    border: 3px solid #2f2f28;
    padding: 15px;
    border-radius: 15px;
    width: 80%;
    font-size: 16px;
    padding-left: 50px;
}
.search svg{
    width: 18px;
    height: 18px;
    position: absolute;
    top: 17px;
    left: 20px;
    opacity: 0.3;
}
.usersList{
    list-style: none;
    padding: 0;
    margin: 0;
    overflow: auto;
    height: calc(100% - 130px);
    margin-top: 30px;
}
.usersList li{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
}
.usersList li > div{
    display: flex;
}
.usersList li img.avatar{
    border-radius: 30px;
    width: 50px;
    height: 50px;
    margin-right: 15px; 
}
.usersList li .action {
    width: 28px;
    height: 28px;
    opacity: 0.3;
    cursor: pointer;
}
.usersList li .action:hover {
    opacity: 0.8;
}
.usersList li h3 {
    margin: 0;
    font-weight: 700;
    margin-left: 15px;
    margin-top: 5px;
    margin-left: 0;
    color: #2f2f28;
}
.usersList li p {
    display: flex;
    align-items: center;
    font-size: 14px;
    margin: 0;
    color: #31a2ac;
    margin-top: 3px;
}
.usersList li p svg {
    width: 16px;
    height: 16px;
    margin-right: 4px;
}
.usersList li p svg path {
    fill: rgba(0, 0, 0, 0.2);
}
.sendInviteBtn {
    background-color: #31a2ac;
    padding: 16px 15px;
    border-radius: 15px;
    font-size: 16px;
    color: #fff;
    width: 100%;
    border: 0;
    outline: none;
    cursor: pointer;
    transition: all 0.1s ease-in-out;
}
.sendInviteBtn:hover {
    background-color: #2bb673;
}
.sendInviteBtn:active {
    transform: translateY(2px);
    box-shadow: none;
}
.successBlock {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    flex-direction: column;
    animation: success-show 0.5s cubic-bezier(0.25, 0.75, 0.5, 1.25);
}
.successBlock img {
    width: 120px;
    height: 120px;
}
.successBlock h3 {
    font-size: 24px;
    margin-bottom: 0;
    margin-top: 10px;
}
.successBlock p {
    text-align: center;
    margin-bottom: 30px;
}
.successBlock button {
    width: 200px;
}
.skeletonList {
    height: calc(100% - 130px);
    margin-top: 30px;
}
.skeletonList svg {
    margin-bottom: 15px;
}
