.randomColor{
	width: 30vw;
    background-color: #2f2f28;
    margin: 7vh auto 0;
    padding: 20px;
    border-radius: 30px;
    position: relative;
}
.randomColor:before, .randomColor:after {
    content: '';
    display: block;
    height: 50px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}
.randomColor:before {
    width: 95%;
    bottom: -10px;
    background-color: #2f2f28a8;
    
}
.randomColor:after {
    width: 90%;
    bottom: -20px;
    background-color: #2f2f2850;
    
}
.randomColor h2{
    font-size: 2.0vw;
    font-weight: 700;
	color: #f0effe;
}
.randomColor p{
    font-size: 1.4vw;
    font-weight: 700;
	color: #f0effe;
}

.color{
    height: 7vh;
    width: 14vw;
    margin: 0 auto;
	border: 5px solid #f0effe;
}
.randomColor button{
	height: 7vh;
	width: 14vw;
	z-index: 1;
	background-color: #31a2ac;
	position: relative;
	display: inline-block;
	font-size: 0.8vw;
	padding: 10px 25px;
	color: #f0effe;
	text-decoration: none;
	border: solid 5px #f0effe;
	box-shadow: 10px 10px 0  -5px #af1c1c;
	transition: .3s;
	overflow: hidden;
	text-transform: uppercase;
	letter-spacing: 2px;
    cursor: pointer;
	margin: 0 20px 20px;
}
.randomColor button:before {
	display: inline-block;
	transition: bottom, 0.35s;
	position: absolute;
	transform: skew(-50deg);
	top: 0px;
	left: 260px;
	background-color: #af1c1c;
	content: "　";
	width: 300px;
	height: 120px;
	z-index: -1;
}  
.randomColor button:hover {
	box-shadow: 0px 0px 0  0px #31a2ac;
}
.randomColor button:hover:before {
	top: -40px;
	left: -40px;
}