.navbar{
    background-color: #af1c1c;
    border-top: 3px solid #31a2ac;
    border-bottom: 3px solid #31a2ac;
}
.content{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 10vh;
    max-width: 1200px;
    margin: 0 auto;
}
.btn{
	z-index: 1;
	background-color: transparent;
	position: relative;
	display: inline-block;
	font-size: 18px;
	padding: 10px 25px;
	color: #f0effe;
	text-decoration: none;
	border: solid 5px #2f2f28;
	box-shadow: 12px 12px 0  -5px #31a2ac;
	transition: .3s;
	overflow: hidden;
	text-transform: uppercase;
	letter-spacing: 2px;
    outline: 2px solid #f0effe;
	cursor: pointer;
}
.btn:before {
	display: inline-block;
	transition: bottom, 0.35s;
	position: absolute;
	transform: skew(-50deg);
	top: 0px;
	left: 260px;
	background-color: #31a2ac;
	content: "　";
	width: 300px;
	height: 120px;
	z-index: -1;
}  
.btn:hover {
	box-shadow: 0px 0px 0  0px #31a2ac;
}
.btn:hover:before {
	top: -40px;
	left: -40px;
}
@media (max-width: 1000px) {
    .content {
        display: block;
		height: auto;
    }
	.btn {
		margin: 10px;
	}
}