.mySkills{
    background-color: #f0effe;
    border-top: 3px solid #2f2f28;
    min-height: 75vh;
}
.container{
    display: flex;
    justify-content: space-between;
    max-width: 1000px;
    margin: 7vh auto 0;
}
.container div ul li{
    font-size: 1.4vw;
	font-weight: 700;
}
.img{
    height: 200px;
    width: 200px;
    margin: 50px auto 0;
    transition: 0.5s;
}
.mySkills{
	animation-duration: 2s;
	animation-name: opacity;
}
@keyframes opacity {
	from {
	  opacity: 0;
	}
  
	to {
	  opacity: 1;
}
}
@media (max-width: 1000px) {
    .container {
        display: block;
        margin: 0 auto;
    }
    .container div ul li{
        font-size: 20px;
        max-width: 200px;
        margin: 0 auto;
    }
    .img{
        margin: 10px auto;
    }
}

    
