.contact{
    min-height: 75vh;
    display: flex;
    justify-content: center;
    background-color: #f0effe;
    border-top: 3px solid #2f2f28;
}
.contact div p{
    margin: 10vh auto 5vh;
    font-size: 1.8vw;
	font-weight: 700;
	color: #2f2f28;
    max-width: 1200px;
}
.contact div h2{
    margin: 15vh auto 0;
    font-size: 2.2vw;
	font-weight: 700;
	color: #2f2f28;
}
.contact div h3{
	color: #2f2f28;
	font-weight: 700;
	font-size: 1.8em;
}
.contact div div{
    font-size: 2.2vw;
	font-weight: 700;
	color: #2f2f28;
}
.btn{
	z-index: 1;
	background-color: #af1c1c;
	position: relative;
	display: inline-block;
	font-size: 18px;
	padding: 10px 25px;
	color: #f0effe;
	text-decoration: none;
	border: solid 5px #f0effe;
	box-shadow: 15px 15px 0  -5px #31a2ac;
	transition: .3s;
	overflow: hidden;
	text-transform: uppercase;
	letter-spacing: 2px;
    outline: 5px solid #2f2f28;
	cursor: pointer;
}
.btn:before{
	display: inline-block;
	transition: bottom, 0.35s;
	position: absolute;
	transform: skew(-50deg);
	top: 0px;
	left: 260px;
	background-color: #31a2ac;
	content: "　";
	width: 300px;
	height: 120px;
	z-index: -1;
}  
.btn:hover{
	box-shadow: 0px 0px 0  0px #31a2ac;
}
.btn:hover:before {
	top: -40px;
	left: -40px;
}
.contact{
	animation-duration: 2s;
	animation-name: opacity;
}
  
@keyframes opacity {
	from {
	  opacity: 0;
	}
  
	to {
	  opacity: 1;
}
}
@media (max-width: 1000px) {
    .contact div p,
	.contact div h2,
	.contact div h3{
       font-size: 30px;
	   padding: 10px;
	   margin: 20px auto;
    }
}