.aboutMe{
    background-color: #f0effe;
    border-top: 3px solid #2f2f28;
    border-bottom: 1px solid #f0effe;
}
.upContent{
    display: flex;
    justify-content: space-between;
    align-items: center;
	max-width: 1200px;
	margin: 0 auto;
}
.photo{
    margin-top: 5vh;
    border-radius: 50%;
    border: 3px solid #31a2ac;
    outline: 3px solid #2f2f28;
}
.text{
    max-width: 800px;
    background-color: #f0effe;
	margin: 5vh auto;
}
.lowContent{
	max-width: 1200px;
	margin: 7vh auto 0;
}
.info{
	display: grid;
	grid-template-columns: 6fr 6fr;
}
.info p{
	font-size: 1.4vw;
	font-weight: 700;
	color: #2f2f28;
}
.lowContent p{
    font-size: 1.3vw;
	font-weight: 700;
	color: #2f2f28; 
}
.aboutMe{
	animation-duration: 2s;
	animation-name: opacity;
}
  
@keyframes opacity {
	from {
	  opacity: 0;
	}
  
	to {
	  opacity: 1;
}
}
@media (max-width: 1000px) {
    .upContent,
    .info{
       display: block; 
    }
    .photo{
        height: 350px;
    }
    .info p,
    .lowContent p{
        font-size: 20px;
        max-width: 400px;
        margin: 10px auto;
    }
    
}