.home{
    min-height: 75vh;
    background-color: #f0effe;
    border-top: 3px solid #2f2f28;
	border-bottom: 5px solid #31a2ac;
}
.home p,span{
	font-size: 1.4vw;
	font-weight: 700;
	color: #2f2f28;
}
.content{
	max-width: 1200px;
	margin: 0 auto; 
	overflow: hidden;
}
.default {
	color: #af1c1c;
	font-weight: 700;
	font-size: 1.8vw;
}
.languageNavbar{
    max-width: 750px;
    margin: 50px auto 0;
	display: flex;
}
.btn{
	height: 100%;
	width: 50%;
	z-index: 1;
	background-color: #31a2ac;
	position: relative;
	display: inline-block;
	font-size: 1.4vw;
	padding: 10px 25px;
	color: #f0effe;
	text-decoration: none;
	border: solid 5px #2f2f28;
	box-shadow: 10px 10px 0  -5px #af1c1c;
	transition: .3s;
	overflow: hidden;
	text-transform: uppercase;
	letter-spacing: 2px;
    cursor: pointer;
	margin: 0 20px;
}
.btn:before {
	display: inline-block;
	transition: bottom, 0.35s;
	position: absolute;
	transform: skew(-50deg);
	top: 0px;
	left: 260px;
	background-color: #af1c1c;
	content: "　";
	width: 300px;
	height: 120px;
	z-index: -1;
}  
.btn:hover {
	box-shadow: 0px 0px 0  0px #31a2ac;
}
.btn:hover:before {
	top: -40px;
	left: -40px;
}
.btn:focus{
	box-shadow: 0px 0px 0  0px #31a2ac;
	background-color: #af1c1c;
}
.home{
	animation-duration: 2s;
	animation-name: opacity;
}
  
@keyframes opacity {
	from {
	  opacity: 0;
	}
  
	to {
	  opacity: 1;
}
}
@media (max-width: 1000px) {
    .languageNavbar{
		display: block;
		max-width: 400px;
		margin: 10px auto;
	}
	.btn{
		margin: 10px;
	}
    .content p,
	.default,
	span{
		font-size: 18px;
		max-width: 500px;
		margin: 10px auto
	}
	.btn{
		font-size: 20px;
	}
}